<template>
    <div class="qd-card">
        <slot></slot>
        <div class="cardTitle">{{ title }}</div>
        <div class="cardSubTitle">{{ subTitle }}</div>
    </div>
</template>

<script>
    import Vue from 'vue';

    export default {
        name: "QdCard",
        props: {
            title: {
                type: String,
                default: '',
                required: true
            },
            subTitle: {
                type: String,
                default: '',
                required: true
            }
        },
        data() {
            return {

            }
        },
        mounted() {

        },
    }
</script>

<style scoped lang="less">
    .qd-card {
        text-align: center;
        .cardTitle {
            margin-top: 5px;
            font-size: 14px;
            font-weight: bold;
        }
        .cardSubTitle {
            margin-top: 5px;
            font-size: 12px;
        }
    }
</style>


